import '/imports/startup/server';
import '/imports/startup/both';
import {Email} from 'meteor/email';

Accounts.emailTemplates.siteName = "dorasdream.com";
Accounts.emailTemplates.from = "Dorasdream Admin <joc98@163.com>";
Accounts.emailTemplates.enrollAccount.subject = function (user) {
    return "Welcome to Awesome Town, " + user.profile.name;
};
Accounts.emailTemplates.enrollAccount.text = function (user, url) {
    return "You have been selected to participate in building a better future!"
           + " To activate your account, simply click the link below:\n\n"
           + url;
};
Accounts.emailTemplates.resetPassword.from = function () {
    // Overrides value set in Accounts.emailTemplates.from when resetting passwords
    return "AwesomeSite Password Reset <joc98@163.com>";
};